<template>
<div class="main">
    <div class="container">
        <div class="title">
            <span>W</span>EAK
        </div>
        <div class="search">
            <div class="engine" @click.stop="enginesShow=!enginesShow">
                <img :src="engine.img" alt="">
                <div class="engines" v-if="enginesShow">
                    <div v-for="(item,index) in engines" :key="item.id" @click="engineChange(index)">
                        <img :src="item.img" alt="">
                        {{ item.name }}
                    </div>
                </div>
            </div>
            <input type="text" v-model="text" ref="refInput" @keyup.enter="search" @keyup="getLegends">
            <div class="clear" v-show="clearShow" @click="clearText"></div>
            <div class="button" @click="search">搜索</div>
            <div class="legends" v-if="legendsShow">
                <div class="legend" v-for="(item,index) in legends.value" @click="legendSearch(item)" :key="index">{{ item }}</div>
            </div>
        </div>
        <Icons/>
        <transition name="linkShow">
            <Link v-if="linkShow"/>
        </transition>
        <div class="mask" v-if="linkShow" @click="cancel"></div>
    </div>
</div>
</template>

<script>
/* eslint-disable */
import Icons from './Icons.vue'
import Link from './Link.vue'
import {useStore} from 'vuex'
import store from '@/store'
import {ref,computed,onMounted,reactive} from 'vue'

export default{
    name:'Main',
    components:{
        Icons,Link
    },
    setup(){
        const vuex=useStore()
        let engines=reactive([{
            name:'Bing',
            img:'https://cn.bing.com/favicon.ico',
            key:'https://cn.bing.com/search?q='
        },{
            name:'百度',
            img:'https://www.baidu.com/favicon.ico',
            key:'https://www.baidu.com/s?wd='
        },{
            name:'Google',
            img:'https://www.google.com/favicon.ico',
            key:'https://www.google.com/search?q='
        }])
        let engine=localStorage.getItem('engine')?reactive(JSON.parse(JSON.stringify(engines.filter(el=>{
            return el.name==localStorage.getItem('engine')
        })[0]))):reactive(JSON.parse(JSON.stringify(engines[1])))
        let text=ref('')
        let refInput=ref()
        let linkShow=computed(()=>{
            return store.state.linkShow
        })
        let clearShow=computed(()=>{
            if(text.value==''){
                return false
            }else{
                return true
            }
        })
        let enginesShow=ref(false)
        let legends=reactive({
            value:[]
        })
        let legendsShow=computed(()=>{
            if(legends.value.length>0 && text.value!='')return true
            else return false
        })
        let cancel=()=>{
            vuex.commit('linkShowChange')
        }
        let clearText=()=>{
            text.value=''
            refInput.value.focus()
        }
        let engineChange=(index)=>{
            localStorage.setItem('engine',engines[index].name)
            engine.name=engines[index].name
            engine.img=engines[index].img
            engine.key=engines[index].key
        }
        let search=()=>{
            window.open(engine.key+text.value,'_blank')
        }
        window.baidu = {
            sug: function(json) {
                legends.value=json.s
            }
        }
        function baiduSug(str){
            if(str){
                let sug=document.createElement('script')
                sug.src=`http://suggestion.baidu.com/su?wd=${str}&cb=window.baidu.sug`
                document.getElementsByTagName('body')[0].appendChild(sug)
            }
            else legends.value=[]
        }
        let legendTimer=setTimeout(()=>{},500)
        let getLegends=()=>{
            clearTimeout(legendTimer)
            legendTimer=setTimeout(()=>{
                baiduSug(text.value)
            },500)
        }
        let legendSearch=(word)=>{
            text.value=word
            search()
        }
        onMounted(()=>{
            addEventListener('click',e=>{
                if(enginesShow.value){
                    enginesShow.value=false
                }
            })
        })
        return{
            linkShow,
            text,
            clearShow,
            refInput,
            engines,
            engine,
            enginesShow,
            legends,
            legendsShow,
            cancel,
            clearText,
            engineChange,
            search,
            getLegends,
            legendSearch
        }
    }
}
</script>

<style lang="less" scoped>
.main{
    display:flex;
    justify-content: center;
    align-items: center;
    width:100vw;
    height:100vh;
    .container{
        .title{
            margin-left:20px;
            font-size:20px;
            font-weight: 700;
            span{
                color:#ff9900;
                font-size:20px;
                font-weight: 700;
            }
        }
        .search{
            display:flex;
            position:relative;
            justify-content: flex-end;
            align-items: center;
            width:600px;
            height:40px;
            border-radius: 20px;
            background-color: #fff;
            padding:0 7px 0 0px;
            .engine{
                position:absolute;
                left:0;
                cursor:pointer;
                display:flex;
                align-items: center;
                img{
                    width:30px;
                    height:30px;
                    margin:0 10px;
                }
                .engines{
                    position:absolute;
                    top:40px;
                    background-color: #fff;
                    color:black;
                    div{
                        padding:5px 15px;
                        border-top:1px solid #000;
                        display:flex;
                        align-items: center;
                        img{
                            margin:0 10px 0 0;
                        }
                    }
                    div:first-of-type{
                        border-top:0;
                    }
                    div:hover{
                        background-color: #d0d0d0;
                    }
                }
            }
            input{
                position:absolute;
                left:50px;
                background-color: #fff;
                border:0;
                outline:0;
                color:black;
                font-weight: 700;
                width:430px;
            }
            .button{
                background-color: #ff9900;
                color:rgb(0,0,0);
                font-size:16px;
                font-weight:700;
                cursor:pointer;
                padding:5px 20px;
                border-radius: 20px;
                user-select: none;
                box-shadow: 0 0 5px 3px rgba(0,0,0,.5);
            }
            .button:active{
                background-color: #bc7100;
                box-shadow: 0 0 5px 1px rgba(0,0,0,.5);
            }
            .clear{
                width:20px;
                height:20px;
                background: url(../assets/clear.png);
                background-size: cover;
                cursor:pointer;
                margin-right:15px;
            }
            .clear:hover{
                background: url(../assets/clearHover.png);
                background-size: cover;
            }
            .legends{
                background-color: #fff;
                color:#000;
                position:absolute;
                top:40px;
                left:20px;
                width:560px;
                .legend{
                    padding:5px 15px;
                    cursor:pointer;
                    border-top:1px solid #000;
                }
                .legend:hover{
                    background-color: #d0d0d0;
                }
            }
        }
    }
}
@keyframes slideIn_top{
    0%{
        top:-250px;
    }
    100%{
        top:50px;
    }
}
@keyframes slideOut_top{
    0%{
        top:50px;
    }
    100%{
        top:-250px;
    }
}
.linkShow-enter-active{
    animation:slideIn_top 0.5s;
}
.linkShow-leave-active{
    animation:slideOut_top 0.5s;
}
.mask{
    position:absolute;
    width:100%;
    height:100%;
    left:0;
    top:0;
    background-color: rgba(27,27,27,.5);
    backdrop-filter: blur(2px);
}

</style>