<template>
    <div class="icons">
        <TransitionGroup name="icons">
            <div v-for="(item,index) in list" class="item" :key="index" 
            @click="pageChange(item)"
            @contextmenu.prevent.stop="rightClick($event,index)"
            @mouseenter="iconover(item)"
            @mouseleave="iconout($event)"
            @mousemove="iconmove($event)"> 
                <img :src="item.img" alt="">
                <div class="onerror"></div>
            </div>
        </TransitionGroup>
        <div class="add item" @click="addIcon">
            <svg t="1685264354036" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2374" width="30" height="30"><path d="M925.696 384q19.456 0 37.376 7.68t30.72 20.48 20.48 30.72 7.68 37.376q0 20.48-7.68 37.888t-20.48 30.208-30.72 20.48-37.376 7.68l-287.744 0 0 287.744q0 20.48-7.68 37.888t-20.48 30.208-30.72 20.48-37.376 7.68q-20.48 0-37.888-7.68t-30.208-20.48-20.48-30.208-7.68-37.888l0-287.744-287.744 0q-20.48 0-37.888-7.68t-30.208-20.48-20.48-30.208-7.68-37.888q0-19.456 7.68-37.376t20.48-30.72 30.208-20.48 37.888-7.68l287.744 0 0-287.744q0-19.456 7.68-37.376t20.48-30.72 30.208-20.48 37.888-7.68q39.936 0 68.096 28.16t28.16 68.096l0 287.744 287.744 0z" p-id="2375"></path></svg>
        </div>
    </div>
    <transition name="menu">
        <div class="menu" v-if="menuShow" :style="{top:mouse.y+'px',left:mouse.x+'px'}">
            <div class="edit" @click="edit">编辑</div>
            <div class="del" @click="del">删除</div>
        </div>
    </transition>
    <div class="tip" v-if="tip.show" :style="{top:tip.y+'px',left:tip.x+'px'}"
    @mouseleave="iconout($event)">
        {{ tip.content }}
    </div>
    
</template>

<script>
/* eslint-disable */
import {reactive,ref,onMounted,computed} from 'vue'
import {useStore} from 'vuex'
import store from '@/store'
export default{
    name:'Icons',
    setup(){
        const vuex=useStore()
        let now=ref(0)
        let mouse=reactive({
            x:0,
            y:0
        })
        let menuShow=ref(false)
        let list=computed(()=>{
            return store.state.icons
        })
        //let list=reactive(vuex.commit('getIcons'))
        let tip=reactive({
            show:false,
            content:'',
            x:0,
            y:0
        })
        let addIcon=()=>{
            vuex.commit('setLinkMethod','add')
            vuex.commit('setCurrentIcon',{name:'',link:''})
            vuex.commit('linkShowChange')
        }
        let pageChange=(icon)=>{
            window.open(icon.link,'_blank')
        }
        let rightClick=(event,index)=>{
            now.value=index
            mouse.x=event.x
            mouse.y=event.y
            menuShow.value=true
        }
        let del=()=>{
            vuex.commit('delIcon',now.value)
        }
        let edit=()=>{
            vuex.commit('setLinkMethod','edit')
            vuex.commit('setIconIndex',now.value)
            vuex.commit('setCurrentIcon',list.value[now.value])
            vuex.commit('linkShowChange')
        }
        let timer=setTimeout(()=>{})
        let iconover=(item)=>{
            tip.content=item.name
        }
        let iconmove=(event)=>{
            tip.show=false,
            clearTimeout(timer)
            timer=setTimeout(()=>{
                tip.show=true
                tip.x=event.x
                tip.y=event.y
            },1000)
        }
        let iconout=(event)=>{
            if(event.toElement){
                if(event.toElement.className!='tip'){
                    tip.show=false
                    clearTimeout(timer)
                }
            }
            else{
                tip.show=false
                clearTimeout(timer)
            }
        }
        onMounted(()=>{
            addEventListener('click',e=>{
                menuShow.value=false
            })
        })
        return {
            list,
            mouse,
            menuShow,
            tip,
            addIcon,
            pageChange,
            rightClick,
            del,
            edit,
            iconover,
            iconmove,
            iconout
        }
    }
}
</script>

<style lang="less" scoped>
.icons{
    display:flex;
    flex-wrap:wrap;
    align-content: flex-start;
    width:600px;
    height:230px;
    margin-top:5px;
    //background-color: #ffffff4d;
    .item{
        width:80px;
        height:80px;
        padding:15px;
        background-color: #ffffff82;
        border-radius:10px;
        margin:10px;
        cursor:pointer;
        img{
            width:100%;
            height:100%;
        }
    }
    .item:hover{
        box-shadow: 0 0 5px 3px rgb(255, 255, 255);
    }
    .item:active{
        box-shadow: 0 0 0px 0px rgb(255, 255, 255);
    }
    .add{
        display: flex;
        justify-content: center;
        align-items: center;
        svg{
            background-color: #ffffff00;
        }
    }
}
.menu{
    background-color: #fff;
    width:80px;
    height:60px;
    position:absolute;
    border-radius:5px;
    div{
        display:flex;
        justify-content: center;
        align-items: center;
        width:100%;
        height:50%;
        color:black;
        cursor:pointer;
    }
    .edit{
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
    }
    .edit:hover{
        background-color: #ff9900;
    }
    .del{
        border-top:1px solid #000;
        border-bottom-left-radius:5px;
        border-bottom-right-radius:5px;
    }
    .del:hover{
        background-color: rgb(211, 85, 85);
    }
}
.tip{
    position:absolute;
    background-color: #fff;
    color:black;
    padding:5px 15px;
}
@keyframes fade_in{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}
@keyframes fade_out{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
.menu-enter-active{
    animation: fade_in 0.3s;
}
.menu-leave-active{
    animation: fade_out 0.3s;
}
@keyframes fade_bottom2top_in{
    0%{
        opacity: 0;
        transform: translate(0,20px);
    }
    100%{
        opacity: 1;
        transform: translate(0,0);
    }
}
@keyframes fade_top2bottom_out{
    0%{
        opacity: 1;
        transform: translate(0,0);
    }
    100%{
        opacity: 0;
        transform: translate(0,20px);
    }
}
.icons-enter-active{
    animation: fade_bottom2top_in 0.5s;
}
.icons-leave-active{
    animation: fade_top2bottom_out 0.2s;
}
</style>