import {createStore} from 'vuex'

export default createStore({
    state:{
        icons:JSON.parse(localStorage.getItem("icons")) ? JSON.parse(localStorage.getItem("icons")) : [],
        linkShow:false,
        currentIcon:{
            name:'',
            link:''
        },
        iconIndex:0,
        linkMethod:'add'
    },
    getters:{

    },
    mutations:{
        addIcon(state,icon){
            state.icons.push(icon)
            localStorage.setItem('icons',JSON.stringify(state.icons))
            //state.icons=JSON.parse(localStorage.getItem("icons"))
        },
        delIcon(state,index){
            state.icons.splice(index,1)
            localStorage.setItem('icons',JSON.stringify(state.icons))
        },
        updateIcon(state,icon){
            state.icons[state.iconIndex]=icon
            localStorage.setItem('icons',JSON.stringify(state.icons))
        },
        linkShowChange(state){
            state.linkShow=!state.linkShow
        },
        setCurrentIcon(state,icon){
            state.currentIcon.name=icon.name
            state.currentIcon.link=icon.link
        },
        setLinkMethod(state,method){
            state.linkMethod=method
        },
        setIconIndex(state,index){
            state.iconIndex=index
        }
    },
    actions:{

    }
})