<template>
    <div class="link">
        <span>名称</span>
        <input type="text" v-model="icon.name">
        <span>链接</span>
        <input type="text" v-model="icon.link">
        <div class="button">
            <button class="cancel" @click="cancel">取消</button>
            <button class="confirm" @click="confirm">确定</button>
        </div>
    </div>
</template>
<script>
/* eslint-disable */
import {useStore} from 'vuex'
import {reactive} from 'vue'
export default{
    name:'Link',
    setup(){
        const vuex=useStore()
        let icon=reactive(vuex.state.currentIcon)
        let cancel=()=>{
            vuex.commit('linkShowChange')
        }
        let confirm=()=>{
            if(vuex.state.linkMethod=='add'){
                icon.name=icon.name.trim()
                icon.link=icon.link.trim()
                let s=icon.link.replace('https://','')
                s=s.replace('http://','')
                s=s.split('/')
                icon.link='http://'+s[0]
                icon.img='http://'+s[0]+'/favicon.ico'
                vuex.commit('addIcon',icon)
                vuex.commit('linkShowChange')
            }
            else if(vuex.state.linkMethod=='edit'){
                icon.name=icon.name.trim()
                icon.link=icon.link.trim()
                let s=icon.link.replace('https://','')
                s=s.replace('http://','')
                s=s.split('/')
                icon.link='http://'+s[0]
                icon.img='http://'+s[0]+'/favicon.ico'
                vuex.commit('updateIcon',icon)
                vuex.commit('linkShowChange')
            }
        }
        return {
            icon,
            cancel,
            confirm
        }
    }
}
</script>
<style lang="less" scoped>
.link{
    position:absolute;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding:20px;
    top:50px;
    left:50%;
    transform: translate(-50%,0);
    width:400px;
    height:200px;
    background-color: #ffffff82;
    color:black;
    border-radius:20px;
    font-weight:700;
    z-index:1;
    span{
        width:15%;
        height:30px;
        display: flex;
        align-items: center;
    }
    input{
        border:0;
        outline:0;
        border-radius: 20px;
        height:30px;
        width:85%;
        padding-left:20px;
        font-weight:700;
    }
    .button{
        width:100%;
        display: flex;
        justify-content: flex-end;
        button{
            margin-left:20px;
            padding:5px 15px;
            font-weight:700;
            cursor:pointer;
            border-radius:3px;
        }
        .cancel{
            background-color: #fff;
        }
        .cancel:active{
            background-color: #ff9000;
        }
        .confirm{
            background-color: #ff9000;
        }
        .confirm:active{
            background-color: #bc7100;
        }
    }
}

</style>